import Vue from 'vue'

const filters = {
    //   ->     
    moneyDec(target) {
        return (target / 100).toFixed(2)
    },

    //   -> 
    money(target) {
        return target / 100
    }
}

Object.keys(filters).forEach(key => {
    Vue.filter(key, filters[key])
})